.contentWrap{
    width: 300px;
    height: 320px;
    box-shadow: 8px 8px 6px 1px var(--shadow);
    overflow: hidden;
    border-radius: 5px;
    background-color: #303841;
}

.archivosContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 20px;
    max-width: 1400px;
}
.archivosContainer::after {
    content: "";
    width: 300px;
}
.archivo{
    height: 70%;
    width: 100%;
    overflow: hidden;
}

.archivoPlaceHolder{
    width: 300px;
    height: 180px;
    margin: 0;
    border-radius: 5px 5px 0 0;
}

.iframeBottom{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    height: 30%;
    margin-top: -6px;
    font-size: 12px;
    padding: 0 10px 4px;
    background-color: rgb(0,51,47);
}
.displayTitle{
    width: 100%;
    text-align: center;
    font-size: 20px;
    text-shadow: 2px 2px 2px #1e1e1e;
    border-bottom: 1px solid rgba(255, 255, 255, 0.411);
    color: white;
}
.der{
    text-align: center;
}
.izq{
    width: 210px;
    overflow: hidden;
}

.contentWrap p {
    margin: 0;
}

.like{
    border-color: transparent;
    background-color: transparent;
}

.liked:hover{
    opacity: 0.4;
}

.toLike{
    opacity: 0.4;
}
.toLike:hover{
    opacity: 0.8;
}

.like:disabled{
    border-radius: 100%;
    background: radial-gradient(rgba(199, 28, 28, 0.85), transparent 80%);
    cursor: progress;
}
.pagination{
    margin-bottom: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style-type: none;
    padding: 0 5rem;
}
.paginationPage{
    width: 2.5rem;
    cursor: pointer;
    border-right: 1px solid var(--blanco);
    user-select: none;
    text-align: center;
}


.pagination li a {
    padding: 0.1rem 0.9rem;
    color: white;
    min-width: 32px;
}

.fastMove{
    cursor: pointer;
    padding: 0.1rem 1rem;
    border-right: 1px solid var(--blanco);
    user-select: none;
}
.fastMove:last-of-type{
    border-right: none;
}

.activePage{
    background-color: var(--fondo-light);
}